@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.session-card-wrapper {
  padding: 0 10px 0 0;
  border-radius: 5px;

  .session-card {
    padding: 14px 16px;
    border: 1px solid color.adjust(pick-color('primary', 'web'), $lightness: 25%);
    grid-template-columns: 240px 180px 40px;
    margin-left: 0;

    span {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .sc-dropdown-toggle-wrapper {
      justify-self: flex-end;

      span {
        font-weight: 400;
      }

      .dropdown-toggle-btn {
        @include circles(32px, color.adjust(pick-color('primary', 'web'), $lightness: 15%));
        border-radius: 4px !important;
        transition: all 0.2s ease;

        .toggle-icon {
          width: 20px;
          height: 20px;
          font-size: 20px;
          transition: all 0.2s ease;
          color: white;
        }

        &.toggled {
          background-color: pick-color('primary', 'web');

          .toggle-icon {
            transform: rotate(-180deg);
          }
        }
      }
    }
  }
}
